import React, { useState } from 'react';
import { Button } from '@mui/material';
import './startup.css';
import LoginIcon from '@mui/icons-material/Login';
import {useNavigate} from 'react-router-dom';
export const Startup = () => {
    const navigate = useNavigate();
    const auth = () => {
        navigate("/auth")
      }
    return(
        <div className='Startup'>
            <div className='div1' >
            <h1 className='text000'>TRACKIFY</h1>
            </div>
            <div>
                <p className='text111'>Yours daily expense tracker</p>
            </div>
            <div >
            <Button className='button' onClick={auth} variant="outline" style={{color:'#0084fe', marginTop: 26 }}>Login/Register</Button>
                </div>
        </div>
    )
}